:root {
    --primary: #fff;
}

.btn {
    padding: 8px 20px;
    border-radius: 2px;
    outline: none;
    border: none;
    cursor: pointer;
}

.btn--primary{
    background-color: var(--primary);
    color:darkslateblue;
    border: 1px solid var(--primary);
}

.btn--outline{
    background-color: transparent;
    color:chartreuse;
    padding: 8px 20px;
    border: 1px solid var(--primary);
    transition: all 0.3 ease-out;
}

.btn--medium {
    padding: 8px 20px;
    font-size: 20px;
}

.btn--large {
    padding: 12px 94px;
    font-size: 20px;
}

.btn--medium:hover, .btn--large:hover{
    background:#fff;
    color:darkslateblue;
    transition: all 0.3 ease-out;
}

